import { ajax } from "@/common/js/ajax";

export function getNickName() {
  return ajax({
    api: '/getThisUserInfo',
    method: 'get'
  });
}

export function uploadAvatar(avatarData) {
  return ajax({
    api: '/uploadAvatar',
    method: 'post',
    data: avatarData
  });
}



